.intro {
  background-color: $primary;
  background-image: url(".././img/intro1x.jpg");
  background-repeat: no-repeat;
  height: 463px;
  width: 100%;
  object-fit: cover;
  background-size: 100% 100%;
  display: grid;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));


}

.intro h1 {
  max-width: 285px;
  margin: 0 auto;
  color: white;
  font-weight: 700;
  text-align: center;
  font-size: 45px;
  line-height: 58px;
  letter-spacing: 0.03em;
  align-self: flex-end;
//  display: inline-block;
  vertical-align: middle;
//  height: 100%;




}

.intro p {
  max-width: 260px;
  margin: 0 auto;
  color: white;
  font-weight: 400;
  text-align: center;
  font-size: 38px;
  line-height: 49px;
  font-style: italic;
  align-self: flex-start;

}




